"use client";

import { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Badge } from "@/components/ui/badge";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { generatePDF } from "@/lib/pdfGenerator";

const formSchema = z.object({
  consumoDiario: z.number().min(0, "Valor deve ser positivo"),
  consumoMensal: z.number().min(0, "Valor deve ser positivo"),
  demandaMaxForaPonta: z.number().min(0, "Valor deve ser positivo"),
  demandaMaxPonta: z.number().min(0, "Valor deve ser positivo"),
  demandaContratadaUnica: z.number().min(0, "Valor deve ser positivo"),
  autonomiaDesejada: z.number().min(0, "Valor deve ser positivo"),
  investimentoBESS: z.number().min(0, "Valor deve ser positivo"),
  custoDiesel: z.number().min(0, "Valor deve ser positivo"),
  consumoDiarioDiesel: z.number().min(0, "Valor deve ser positivo"),
  comDiesel: z.number().min(0, "Valor deve ser positivo"),
  consumoDieselPorKwh: z.number().min(0, "Valor deve ser positivo"),
  addConsumoForaPontaBESS: z.number().min(0, "Valor deve ser positivo"),
  custoManutencaoGerador: z.number().min(0, "Valor deve ser positivo"),
  consumoKwhDiarioPonta: z.number().min(0, "Valor deve ser positivo"),
  custoAnualUltrapassagens: z.number().min(0, "Valor deve ser positivo"),
  capacidadeUtilBESS: z.number().min(0, "Valor deve ser positivo"),
  bessSugerido: z.number().min(0, "Valor deve ser positivo"),
});

type FormData = z.infer<typeof formSchema>;

interface CalculationResults {
  energiaNecessaria: number;
  autonomia: number;
  consumoDieselLitros: number;
  custoDieselMensal: number;
  custoAnualGerador: number;
  paybackSimples: number;
  paybackComCustoEvitado: number;
}

export default function PaybackCalculator() {
  const [results, setResults] = useState<CalculationResults | null>(null);
  const [isCalculating, setIsCalculating] = useState(false);
  const [selectedEfficiency, setSelectedEfficiency] = useState<string>("85");
  const [calculatedAutonomy, setCalculatedAutonomy] = useState<number | null>(null);

  const {
    register,
    handleSubmit,
    reset,
    setValue,
    watch,
    formState: { errors },
  } = useForm<FormData>({
    resolver: zodResolver(formSchema),
  });

  // Watch for changes in relevant fields
  const watchedFields = watch(["bessSugerido", "consumoDiario"]);

  // Calculate autonomy when efficiency or relevant fields change
  useEffect(() => {
    const [bessSugerido, consumoDiario] = watchedFields;
    
    if (bessSugerido && consumoDiario && selectedEfficiency) {
      const efficiency = parseFloat(selectedEfficiency) / 100;
      const consumoPorHora = consumoDiario / 24;
      const autonomy = (bessSugerido / consumoPorHora) * efficiency;
      
      setCalculatedAutonomy(autonomy);
      setValue("autonomiaDesejada", autonomy);
    }
  }, [watchedFields, selectedEfficiency, setValue]);

  const handleEfficiencyChange = (value: string) => {
    setSelectedEfficiency(value);
  };

  const calculatePayback = (data: FormData) => {
    setIsCalculating(true);
    
    // Cálculos conforme especificação
    const consumoPorHora = data.consumoDiario / 24;
    const energiaNecessaria = consumoPorHora * data.autonomiaDesejada;
    const autonomia = data.capacidadeUtilBESS / consumoPorHora;
    const consumoDieselLitros = data.consumoDiarioDiesel * data.consumoDieselPorKwh;
    const custoDieselMensal = consumoDieselLitros * data.custoDiesel * 30;
    const custoAnualGerador = (custoDieselMensal + data.custoManutencaoGerador) * 12;
    
    const economiaAnual = custoAnualGerador + data.custoManutencaoGerador + data.comDiesel - data.addConsumoForaPontaBESS;
    const paybackSimples = data.investimentoBESS / economiaAnual;
    const paybackComCustoEvitado = data.investimentoBESS / (economiaAnual + data.custoAnualUltrapassagens);

    const calculationResults: CalculationResults = {
      energiaNecessaria,
      autonomia,
      consumoDieselLitros,
      custoDieselMensal,
      custoAnualGerador,
      paybackSimples,
      paybackComCustoEvitado,
    };

    setResults(calculationResults);
    setIsCalculating(false);
  };

  const clearForm = () => {
    reset();
    setResults(null);
  };

  const autoFill = () => {
    // Dados de exemplo para demonstração
    setValue("consumoDiario", 1200);
    setValue("consumoMensal", 36000);
    setValue("demandaMaxForaPonta", 150);
    setValue("demandaMaxPonta", 200);
    setValue("demandaContratadaUnica", 180);
    setValue("autonomiaDesejada", 4);
    setValue("investimentoBESS", 500000);
    setValue("custoDiesel", 6.5);
    setValue("consumoDiarioDiesel", 300);
    setValue("comDiesel", 15000);
    setValue("consumoDieselPorKwh", 0.25);
    setValue("addConsumoForaPontaBESS", 8000);
    setValue("custoManutencaoGerador", 2000);
    setValue("consumoKwhDiarioPonta", 400);
    setValue("custoAnualUltrapassagens", 12000);
    setValue("capacidadeUtilBESS", 200);
    setValue("bessSugerido", 240);
  };

  const saveCalculation = async () => {
    if (!results) return;
    
    try {
      const response = await fetch('/api/history', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          timestamp: new Date().toISOString(),
          inputs: register,
          results,
        }),
      });
      
      if (response.ok) {
        alert('Cálculo salvo com sucesso!');
      }
    } catch (error) {
      console.error('Erro ao salvar:', error);
      alert('Erro ao salvar o cálculo');
    }
  };

  const downloadPDF = () => {
    if (!results) return;
    generatePDF(results);
  };

  return (
    <div className="space-y-6">
      <form onSubmit={handleSubmit(calculatePayback)} className="space-y-6">
        {/* Seção de Consumo */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Dados de Consumo</CardTitle>
          </CardHeader>
          <CardContent className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            <div className="space-y-2">
              <Label htmlFor="consumoDiario">Consumo Diário (kWh)</Label>
              <Input
                id="consumoDiario"
                type="number"
                step="0.01"
                {...register("consumoDiario", { valueAsNumber: true })}
              />
              {errors.consumoDiario && (
                <p className="text-sm text-red-500">{errors.consumoDiario.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="consumoMensal">Consumo Mensal (kWh)</Label>
              <Input
                id="consumoMensal"
                type="number"
                step="0.01"
                {...register("consumoMensal", { valueAsNumber: true })}
              />
              {errors.consumoMensal && (
                <p className="text-sm text-red-500">{errors.consumoMensal.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="consumoKwhDiarioPonta">Consumo kWh Diário PONTA</Label>
              <Input
                id="consumoKwhDiarioPonta"
                type="number"
                step="0.01"
                {...register("consumoKwhDiarioPonta", { valueAsNumber: true })}
              />
              {errors.consumoKwhDiarioPonta && (
                <p className="text-sm text-red-500">{errors.consumoKwhDiarioPonta.message}</p>
              )}
            </div>
          </CardContent>
        </Card>

        {/* Seção de Demanda */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Dados de Demanda</CardTitle>
          </CardHeader>
          <CardContent className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            <div className="space-y-2">
              <Label htmlFor="demandaMaxForaPonta">Demanda Máx. Fora Ponta (kW)</Label>
              <Input
                id="demandaMaxForaPonta"
                type="number"
                step="0.01"
                {...register("demandaMaxForaPonta", { valueAsNumber: true })}
              />
              {errors.demandaMaxForaPonta && (
                <p className="text-sm text-red-500">{errors.demandaMaxForaPonta.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="demandaMaxPonta">Demanda Máx. Ponta (kW)</Label>
              <Input
                id="demandaMaxPonta"
                type="number"
                step="0.01"
                {...register("demandaMaxPonta", { valueAsNumber: true })}
              />
              {errors.demandaMaxPonta && (
                <p className="text-sm text-red-500">{errors.demandaMaxPonta.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="demandaContratadaUnica">Demanda Contratada Única (kW)</Label>
              <Input
                id="demandaContratadaUnica"
                type="number"
                step="0.01"
                {...register("demandaContratadaUnica", { valueAsNumber: true })}
              />
              {errors.demandaContratadaUnica && (
                <p className="text-sm text-red-500">{errors.demandaContratadaUnica.message}</p>
              )}
            </div>
          </CardContent>
        </Card>

        {/* Seção BESS */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Dados do BESS</CardTitle>
            <CardDescription>
              Configure a eficiência para calcular automaticamente a autonomia
            </CardDescription>
          </CardHeader>
          <CardContent className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            <div className="space-y-2">
              <Label htmlFor="bessSugerido">BESS Sugerido (kWh)</Label>
              <Input
                id="bessSugerido"
                type="number"
                step="0.01"
                {...register("bessSugerido", { valueAsNumber: true })}
              />
              {errors.bessSugerido && (
                <p className="text-sm text-red-500">{errors.bessSugerido.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="efficiency">Eficiência do Sistema</Label>
              <Select value={selectedEfficiency} onValueChange={handleEfficiencyChange}>
                <SelectTrigger>
                  <SelectValue placeholder="Selecione a eficiência" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="75">75% - Eficiência Básica</SelectItem>
                  <SelectItem value="85">85% - Eficiência Padrão</SelectItem>
                  <SelectItem value="90">90% - Alta Eficiência</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label htmlFor="autonomiaDesejada">Autonomia Calculada (h)</Label>
              <div className="relative">
                <Input
                  id="autonomiaDesejada"
                  type="number"
                  step="0.01"
                  {...register("autonomiaDesejada", { valueAsNumber: true })}
                  className="bg-gray-50"
                  readOnly
                />
                {calculatedAutonomy && (
                  <Badge 
                    variant="secondary" 
                    className="absolute right-2 top-1/2 transform -translate-y-1/2 text-xs"
                  >
                    Auto
                  </Badge>
                )}
              </div>
              {errors.autonomiaDesejada && (
                <p className="text-sm text-red-500">{errors.autonomiaDesejada.message}</p>
              )}
              {calculatedAutonomy && (
                <p className="text-xs text-green-600">
                  Calculado automaticamente: {calculatedAutonomy.toFixed(2)}h com {selectedEfficiency}% de eficiência
                </p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="investimentoBESS">Investimento no BESS (R$)</Label>
              <Input
                id="investimentoBESS"
                type="number"
                step="0.01"
                {...register("investimentoBESS", { valueAsNumber: true })}
              />
              {errors.investimentoBESS && (
                <p className="text-sm text-red-500">{errors.investimentoBESS.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="capacidadeUtilBESS">Capacidade Útil do BESS (kWh)</Label>
              <Input
                id="capacidadeUtilBESS"
                type="number"
                step="0.01"
                {...register("capacidadeUtilBESS", { valueAsNumber: true })}
              />
              {errors.capacidadeUtilBESS && (
                <p className="text-sm text-red-500">{errors.capacidadeUtilBESS.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="addConsumoForaPontaBESS">ADD Consumo Fora Ponta BESS (R$)</Label>
              <Input
                id="addConsumoForaPontaBESS"
                type="number"
                step="0.01"
                {...register("addConsumoForaPontaBESS", { valueAsNumber: true })}
              />
              {errors.addConsumoForaPontaBESS && (
                <p className="text-sm text-red-500">{errors.addConsumoForaPontaBESS.message}</p>
              )}
            </div>
          </CardContent>
          
          {/* Informação sobre o cálculo */}
          <CardContent className="pt-0">
            <Alert>
              <AlertDescription>
                <strong>Fórmula:</strong> Autonomia = (BESS Sugerido ÷ (Consumo Diário ÷ 24)) × Eficiência Selecionada
                <br />
                <strong>Exemplo:</strong> Com BESS de 240 kWh, consumo de 1200 kWh/dia e 85% de eficiência = 4.08h de autonomia
              </AlertDescription>
            </Alert>
          </CardContent>
        </Card>

        {/* Seção Diesel */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Dados do Diesel</CardTitle>
          </CardHeader>
          <CardContent className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            <div className="space-y-2">
              <Label htmlFor="custoDiesel">Custo do Diesel (R$/L)</Label>
              <Input
                id="custoDiesel"
                type="number"
                step="0.01"
                {...register("custoDiesel", { valueAsNumber: true })}
              />
              {errors.custoDiesel && (
                <p className="text-sm text-red-500">{errors.custoDiesel.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="consumoDiarioDiesel">Consumo Diário do Diesel (L)</Label>
              <Input
                id="consumoDiarioDiesel"
                type="number"
                step="0.01"
                {...register("consumoDiarioDiesel", { valueAsNumber: true })}
              />
              {errors.consumoDiarioDiesel && (
                <p className="text-sm text-red-500">{errors.consumoDiarioDiesel.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="comDiesel">COM. DIESEL (R$)</Label>
              <Input
                id="comDiesel"
                type="number"
                step="0.01"
                {...register("comDiesel", { valueAsNumber: true })}
              />
              {errors.comDiesel && (
                <p className="text-sm text-red-500">{errors.comDiesel.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="consumoDieselPorKwh">Consumo Diesel por kWh gerado (L/kWh)</Label>
              <Input
                id="consumoDieselPorKwh"
                type="number"
                step="0.01"
                {...register("consumoDieselPorKwh", { valueAsNumber: true })}
              />
              {errors.consumoDieselPorKwh && (
                <p className="text-sm text-red-500">{errors.consumoDieselPorKwh.message}</p>
              )}
            </div>
          </CardContent>
        </Card>

        {/* Seção Custos */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Outros Custos</CardTitle>
          </CardHeader>
          <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="custoManutencaoGerador">Custo de Manutenção Gerador (R$/mês)</Label>
              <Input
                id="custoManutencaoGerador"
                type="number"
                step="0.01"
                {...register("custoManutencaoGerador", { valueAsNumber: true })}
              />
              {errors.custoManutencaoGerador && (
                <p className="text-sm text-red-500">{errors.custoManutencaoGerador.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="custoAnualUltrapassagens">Custo Anual de Ultrapassagens (R$)</Label>
              <Input
                id="custoAnualUltrapassagens"
                type="number"
                step="0.01"
                {...register("custoAnualUltrapassagens", { valueAsNumber: true })}
              />
              {errors.custoAnualUltrapassagens && (
                <p className="text-sm text-red-500">{errors.custoAnualUltrapassagens.message}</p>
              )}
            </div>
          </CardContent>
        </Card>

        {/* Botões de Ação */}
        <div className="flex flex-wrap gap-4 justify-center">
          <Button type="submit" disabled={isCalculating} className="min-w-32">
            {isCalculating ? "Calculando..." : "Calcular"}
          </Button>
          <Button type="button" variant="outline" onClick={clearForm}>
            Limpar
          </Button>
          <Button type="button" variant="secondary" onClick={autoFill}>
            Auto-preencher
          </Button>
          {results && (
            <>
              <Button type="button" variant="default" onClick={saveCalculation}>
                Salvar
              </Button>
              <Button type="button" variant="default" onClick={downloadPDF}>
                Baixar PDF
              </Button>
            </>
          )}
        </div>
      </form>

      {/* Resultados */}
      {results && (
        <Card className="mt-8">
          <CardHeader>
            <CardTitle className="text-2xl text-center">Resultados do Cálculo</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              <div className="text-center p-4 bg-blue-50 rounded-lg">
                <h3 className="font-semibold text-blue-800">Energia Necessária</h3>
                <p className="text-2xl font-bold text-blue-600">
                  {results.energiaNecessaria.toFixed(2)} kWh
                </p>
              </div>

              <div className="text-center p-4 bg-green-50 rounded-lg">
                <h3 className="font-semibold text-green-800">Autonomia</h3>
                <p className="text-2xl font-bold text-green-600">
                  {results.autonomia.toFixed(2)} h
                </p>
              </div>

              <div className="text-center p-4 bg-yellow-50 rounded-lg">
                <h3 className="font-semibold text-yellow-800">Consumo Diesel</h3>
                <p className="text-2xl font-bold text-yellow-600">
                  {results.consumoDieselLitros.toFixed(2)} L/dia
                </p>
              </div>

              <div className="text-center p-4 bg-red-50 rounded-lg">
                <h3 className="font-semibold text-red-800">Custo Diesel Mensal</h3>
                <p className="text-2xl font-bold text-red-600">
                  R$ {results.custoDieselMensal.toLocaleString('pt-BR', { minimumFractionDigits: 2 })}
                </p>
              </div>

              <div className="text-center p-4 bg-purple-50 rounded-lg">
                <h3 className="font-semibold text-purple-800">Custo Anual Gerador</h3>
                <p className="text-2xl font-bold text-purple-600">
                  R$ {results.custoAnualGerador.toLocaleString('pt-BR', { minimumFractionDigits: 2 })}
                </p>
              </div>

              <div className="text-center p-4 bg-indigo-50 rounded-lg">
                <h3 className="font-semibold text-indigo-800">PAYBACK Simples</h3>
                <p className="text-2xl font-bold text-indigo-600">
                  {results.paybackSimples.toFixed(2)} anos
                </p>
              </div>
            </div>

            <Separator className="my-6" />

            <div className="text-center">
              <Badge variant="default" className="text-lg px-4 py-2">
                PAYBACK com Custo Evitado: {results.paybackComCustoEvitado.toFixed(2)} anos
              </Badge>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
