"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";

interface CalculationRecord {
  id: string;
  timestamp: string;
  name?: string;
  inputs: any;
  results: {
    energiaNecessaria: number;
    autonomia: number;
    consumoDieselLitros: number;
    custoDieselMensal: number;
    custoAnualGerador: number;
    paybackSimples: number;
    paybackComCustoEvitado: number;
  };
}

export default function HistoryManager() {
  const [records, setRecords] = useState<CalculationRecord[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [editingRecord, setEditingRecord] = useState<CalculationRecord | null>(null);
  const [newName, setNewName] = useState("");

  useEffect(() => {
    loadHistory();
  }, []);

  const loadHistory = async () => {
    try {
      setIsLoading(true);
      const response = await fetch('/api/history');
      
      if (!response.ok) {
        throw new Error('Erro ao carregar histórico');
      }
      
      const data = await response.json();
      setRecords(data.records || []);
    } catch (error) {
      console.error('Erro ao carregar histórico:', error);
      setError('Erro ao carregar o histórico de cálculos');
      // Para demonstração, vamos usar dados mock se a API falhar
      setRecords(getMockData());
    } finally {
      setIsLoading(false);
    }
  };

  const getMockData = (): CalculationRecord[] => [
    {
      id: "1",
      timestamp: new Date().toISOString(),
      name: "Cálculo Exemplo 1",
      inputs: {},
      results: {
        energiaNecessaria: 200,
        autonomia: 4,
        consumoDieselLitros: 75,
        custoDieselMensal: 14625,
        custoAnualGerador: 195000,
        paybackSimples: 2.56,
        paybackComCustoEvitado: 2.31,
      }
    },
    {
      id: "2",
      timestamp: new Date(Date.now() - 86400000).toISOString(),
      name: "Cálculo Exemplo 2",
      inputs: {},
      results: {
        energiaNecessaria: 150,
        autonomia: 3,
        consumoDieselLitros: 60,
        custoDieselMensal: 11700,
        custoAnualGerador: 156000,
        paybackSimples: 3.21,
        paybackComCustoEvitado: 2.89,
      }
    }
  ];

  const deleteRecord = async (id: string) => {
    if (!confirm('Tem certeza que deseja excluir este cálculo?')) {
      return;
    }

    try {
      const response = await fetch(`/api/history?id=${id}`, {
        method: 'DELETE',
      });

      if (!response.ok) {
        throw new Error('Erro ao excluir registro');
      }

      setRecords(records.filter(record => record.id !== id));
    } catch (error) {
      console.error('Erro ao excluir:', error);
      setError('Erro ao excluir o registro');
    }
  };

  const updateRecord = async (id: string, name: string) => {
    try {
      const response = await fetch('/api/history', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ id, name }),
      });

      if (!response.ok) {
        throw new Error('Erro ao atualizar registro');
      }

      setRecords(records.map(record => 
        record.id === id ? { ...record, name } : record
      ));
      setEditingRecord(null);
      setNewName("");
    } catch (error) {
      console.error('Erro ao atualizar:', error);
      setError('Erro ao atualizar o registro');
    }
  };

  const formatDate = (timestamp: string) => {
    return new Date(timestamp).toLocaleString('pt-BR');
  };

  const formatCurrency = (value: number) => {
    return value.toLocaleString('pt-BR', { 
      style: 'currency', 
      currency: 'BRL' 
    });
  };

  if (isLoading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="text-lg">Carregando histórico...</div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {error && (
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      <div className="flex justify-between items-center">
        <h3 className="text-lg font-semibold">
          {records.length} cálculo{records.length !== 1 ? 's' : ''} salvo{records.length !== 1 ? 's' : ''}
        </h3>
        <Button onClick={loadHistory} variant="outline" size="sm">
          Atualizar
        </Button>
      </div>

      {records.length === 0 ? (
        <Card>
          <CardContent className="text-center py-12">
            <p className="text-gray-500 mb-4">Nenhum cálculo salvo ainda</p>
            <p className="text-sm text-gray-400">
              Faça um cálculo na aba "Calculadora" e clique em "Salvar" para ver o histórico aqui
            </p>
          </CardContent>
        </Card>
      ) : (
        <div className="grid gap-4">
          {records.map((record) => (
            <Card key={record.id} className="hover:shadow-md transition-shadow">
              <CardHeader className="pb-3">
                <div className="flex justify-between items-start">
                  <div>
                    <CardTitle className="text-lg">
                      {record.name || `Cálculo ${record.id}`}
                    </CardTitle>
                    <CardDescription>
                      {formatDate(record.timestamp)}
                    </CardDescription>
                  </div>
                  <div className="flex gap-2">
                    <Dialog>
                      <DialogTrigger asChild>
                        <Button variant="outline" size="sm">
                          Ver Detalhes
                        </Button>
                      </DialogTrigger>
                      <DialogContent className="max-w-2xl">
                        <DialogHeader>
                          <DialogTitle>
                            {record.name || `Cálculo ${record.id}`}
                          </DialogTitle>
                          <DialogDescription>
                            Detalhes do cálculo realizado em {formatDate(record.timestamp)}
                          </DialogDescription>
                        </DialogHeader>
                        <div className="grid grid-cols-2 gap-4 mt-4">
                          <div className="space-y-2">
                            <h4 className="font-semibold">Resultados Principais</h4>
                            <div className="text-sm space-y-1">
                              <p><strong>Energia Necessária:</strong> {record.results.energiaNecessaria.toFixed(2)} kWh</p>
                              <p><strong>Autonomia:</strong> {record.results.autonomia.toFixed(2)} h</p>
                              <p><strong>Consumo Diesel:</strong> {record.results.consumoDieselLitros.toFixed(2)} L/dia</p>
                            </div>
                          </div>
                          <div className="space-y-2">
                            <h4 className="font-semibold">Custos e Payback</h4>
                            <div className="text-sm space-y-1">
                              <p><strong>Custo Diesel Mensal:</strong> {formatCurrency(record.results.custoDieselMensal)}</p>
                              <p><strong>Custo Anual Gerador:</strong> {formatCurrency(record.results.custoAnualGerador)}</p>
                              <p><strong>PAYBACK Simples:</strong> {record.results.paybackSimples.toFixed(2)} anos</p>
                              <p><strong>PAYBACK c/ Custo Evitado:</strong> {record.results.paybackComCustoEvitado.toFixed(2)} anos</p>
                            </div>
                          </div>
                        </div>
                      </DialogContent>
                    </Dialog>

                    <Dialog>
                      <DialogTrigger asChild>
                        <Button variant="outline" size="sm">
                          Editar
                        </Button>
                      </DialogTrigger>
                      <DialogContent>
                        <DialogHeader>
                          <DialogTitle>Editar Nome do Cálculo</DialogTitle>
                          <DialogDescription>
                            Altere o nome para identificar melhor este cálculo
                          </DialogDescription>
                        </DialogHeader>
                        <div className="space-y-4 mt-4">
                          <div className="space-y-2">
                            <Label htmlFor="record-name">Nome do Cálculo</Label>
                            <Input
                              id="record-name"
                              value={newName || record.name || ''}
                              onChange={(e) => setNewName(e.target.value)}
                              placeholder="Digite um nome para o cálculo"
                            />
                          </div>
                          <div className="flex gap-2">
                            <Button
                              onClick={() => updateRecord(record.id, newName || record.name || '')}
                              className="flex-1"
                            >
                              Salvar
                            </Button>
                            <Button
                              variant="outline"
                              onClick={() => {
                                setEditingRecord(null);
                                setNewName("");
                              }}
                            >
                              Cancelar
                            </Button>
                          </div>
                        </div>
                      </DialogContent>
                    </Dialog>

                    <Button
                      variant="destructive"
                      size="sm"
                      onClick={() => deleteRecord(record.id)}
                    >
                      Excluir
                    </Button>
                  </div>
                </div>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                  <div className="text-center p-3 bg-blue-50 rounded-lg">
                    <p className="text-sm font-medium text-blue-800">PAYBACK Simples</p>
                    <p className="text-xl font-bold text-blue-600">
                      {record.results.paybackSimples.toFixed(2)} anos
                    </p>
                  </div>
                  <div className="text-center p-3 bg-green-50 rounded-lg">
                    <p className="text-sm font-medium text-green-800">PAYBACK c/ Custo Evitado</p>
                    <p className="text-xl font-bold text-green-600">
                      {record.results.paybackComCustoEvitado.toFixed(2)} anos
                    </p>
                  </div>
                  <div className="text-center p-3 bg-yellow-50 rounded-lg">
                    <p className="text-sm font-medium text-yellow-800">Energia Necessária</p>
                    <p className="text-lg font-bold text-yellow-600">
                      {record.results.energiaNecessaria.toFixed(0)} kWh
                    </p>
                  </div>
                  <div className="text-center p-3 bg-purple-50 rounded-lg">
                    <p className="text-sm font-medium text-purple-800">Autonomia</p>
                    <p className="text-lg font-bold text-purple-600">
                      {record.results.autonomia.toFixed(1)} h
                    </p>
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
}
