import jsPDF from 'jspdf';

interface CalculationResults {
  energiaNecessaria: number;
  autonomia: number;
  consumoDieselLitros: number;
  custoDieselMensal: number;
  custoAnualGerador: number;
  paybackSimples: number;
  paybackComCustoEvitado: number;
}

export const generatePDF = (results: CalculationResults) => {
  const doc = new jsPDF();
  
  // Configurações
  const pageWidth = doc.internal.pageSize.width;
  const margin = 20;
  let yPosition = 30;
  
  // Título
  doc.setFontSize(20);
  doc.setFont('helvetica', 'bold');
  doc.text('RELATÓRIO DE PAYBACK BESS', pageWidth / 2, yPosition, { align: 'center' });
  
  yPosition += 20;
  
  // Data
  doc.setFontSize(12);
  doc.setFont('helvetica', 'normal');
  doc.text(`Data: ${new Date().toLocaleDateString('pt-BR')}`, margin, yPosition);
  
  yPosition += 20;
  
  // Linha separadora
  doc.line(margin, yPosition, pageWidth - margin, yPosition);
  yPosition += 15;
  
  // Título dos resultados
  doc.setFontSize(16);
  doc.setFont('helvetica', 'bold');
  doc.text('RESULTADOS DOS CÁLCULOS', margin, yPosition);
  
  yPosition += 15;
  
  // Resultados
  doc.setFontSize(12);
  doc.setFont('helvetica', 'normal');
  
  const resultItems = [
    { label: 'Energia Necessária:', value: `${results.energiaNecessaria.toFixed(2)} kWh` },
    { label: 'Autonomia:', value: `${results.autonomia.toFixed(2)} h` },
    { label: 'Consumo Diesel:', value: `${results.consumoDieselLitros.toFixed(2)} L/dia` },
    { label: 'Custo Diesel Mensal:', value: `R$ ${results.custoDieselMensal.toLocaleString('pt-BR', { minimumFractionDigits: 2 })}` },
    { label: 'Custo Anual Gerador:', value: `R$ ${results.custoAnualGerador.toLocaleString('pt-BR', { minimumFractionDigits: 2 })}` },
    { label: 'PAYBACK Simples:', value: `${results.paybackSimples.toFixed(2)} anos` },
    { label: 'PAYBACK com Custo Evitado:', value: `${results.paybackComCustoEvitado.toFixed(2)} anos` },
  ];
  
  resultItems.forEach((item) => {
    doc.setFont('helvetica', 'bold');
    doc.text(item.label, margin, yPosition);
    doc.setFont('helvetica', 'normal');
    doc.text(item.value, margin + 80, yPosition);
    yPosition += 10;
  });
  
  yPosition += 10;
  
  // Linha separadora
  doc.line(margin, yPosition, pageWidth - margin, yPosition);
  yPosition += 15;
  
  // Conclusão destacada
  doc.setFontSize(14);
  doc.setFont('helvetica', 'bold');
  doc.text('CONCLUSÃO:', margin, yPosition);
  
  yPosition += 10;
  
  doc.setFontSize(12);
  doc.setFont('helvetica', 'normal');
  const conclusao = `O tempo de retorno do investimento (PAYBACK) é de ${results.paybackSimples.toFixed(2)} anos considerando apenas os custos operacionais, ou ${results.paybackComCustoEvitado.toFixed(2)} anos incluindo os custos evitados de ultrapassagem.`;
  
  const splitText = doc.splitTextToSize(conclusao, pageWidth - 2 * margin);
  doc.text(splitText, margin, yPosition);
  
  yPosition += splitText.length * 6 + 15;
  
  // Rodapé
  doc.setFontSize(10);
  doc.setFont('helvetica', 'italic');
  doc.text('Relatório gerado automaticamente pelo Sistema de Cálculo PAYBACK BESS', pageWidth / 2, yPosition, { align: 'center' });
  
  // Salvar o PDF
  const fileName = `payback-bess-${new Date().toISOString().split('T')[0]}.pdf`;
  doc.save(fileName);
};
