"use client";

import { useState, useRef } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Progress } from "@/components/ui/progress";
import { Badge } from "@/components/ui/badge";

interface ExtractedData {
  consumoDiario?: number;
  consumoMensal?: number;
  demandaMaxForaPonta?: number;
  demandaMaxPonta?: number;
  custoAnualUltrapassagens?: number;
  [key: string]: any;
}

export default function PDFUploader() {
  const [file, setFile] = useState<File | null>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [extractedData, setExtractedData] = useState<ExtractedData | null>(null);
  const [error, setError] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = event.target.files?.[0];
    if (selectedFile) {
      if (selectedFile.type !== 'application/pdf') {
        setError('Por favor, selecione apenas arquivos PDF');
        return;
      }
      
      if (selectedFile.size > 10 * 1024 * 1024) { // 10MB limit
        setError('O arquivo deve ter no máximo 10MB');
        return;
      }
      
      setFile(selectedFile);
      setError(null);
      setExtractedData(null);
    }
  };

  const uploadAndProcess = async () => {
    if (!file) return;

    setIsUploading(true);
    setUploadProgress(0);
    setError(null);

    try {
      const formData = new FormData();
      formData.append('pdf', file);

      // Simular progresso
      const progressInterval = setInterval(() => {
        setUploadProgress(prev => {
          if (prev >= 90) {
            clearInterval(progressInterval);
            return prev;
          }
          return prev + 10;
        });
      }, 200);

      const response = await fetch('/api/pdf-read', {
        method: 'POST',
        body: formData,
      });

      clearInterval(progressInterval);
      setUploadProgress(100);

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Erro ao processar o arquivo');
      }

      const data = await response.json();
      setExtractedData(data.extractedData);
      
    } catch (error) {
      console.error('Erro no upload:', error);
      setError(error instanceof Error ? error.message : 'Erro desconhecido ao processar o arquivo');
    } finally {
      setIsUploading(false);
      setUploadProgress(0);
    }
  };

  const clearFile = () => {
    setFile(null);
    setExtractedData(null);
    setError(null);
    setUploadProgress(0);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const applyToCalculator = () => {
    if (!extractedData) return;
    
    // Aqui você pode implementar a lógica para aplicar os dados extraídos
    // ao formulário da calculadora. Por enquanto, vamos mostrar um alerta
    alert('Dados aplicados à calculadora! (Funcionalidade será implementada)');
  };

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>Selecionar Arquivo</CardTitle>
          <CardDescription>
            Faça upload de uma fatura de energia em PDF para extração automática dos dados
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="pdf-upload">Arquivo PDF da Fatura</Label>
            <Input
              id="pdf-upload"
              ref={fileInputRef}
              type="file"
              accept=".pdf"
              onChange={handleFileSelect}
              disabled={isUploading}
            />
          </div>

          {file && (
            <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
              <div className="flex items-center space-x-2">
                <Badge variant="secondary">PDF</Badge>
                <span className="text-sm font-medium">{file.name}</span>
                <span className="text-xs text-gray-500">
                  ({(file.size / 1024 / 1024).toFixed(2)} MB)
                </span>
              </div>
              <Button
                variant="outline"
                size="sm"
                onClick={clearFile}
                disabled={isUploading}
              >
                Remover
              </Button>
            </div>
          )}

          {isUploading && (
            <div className="space-y-2">
              <div className="flex justify-between text-sm">
                <span>Processando arquivo...</span>
                <span>{uploadProgress}%</span>
              </div>
              <Progress value={uploadProgress} className="w-full" />
            </div>
          )}

          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          <div className="flex gap-2">
            <Button
              onClick={uploadAndProcess}
              disabled={!file || isUploading}
              className="flex-1"
            >
              {isUploading ? 'Processando...' : 'Processar Fatura'}
            </Button>
          </div>
        </CardContent>
      </Card>

      {extractedData && (
        <Card>
          <CardHeader>
            <CardTitle className="text-green-600">Dados Extraídos com Sucesso!</CardTitle>
            <CardDescription>
              Os seguintes dados foram identificados na sua fatura de energia
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {Object.entries(extractedData).map(([key, value]) => (
                <div key={key} className="flex justify-between items-center p-2 bg-gray-50 rounded">
                  <span className="font-medium text-sm capitalize">
                    {key.replace(/([A-Z])/g, ' $1').trim()}:
                  </span>
                  <span className="text-sm">
                    {typeof value === 'number' 
                      ? value.toLocaleString('pt-BR', { minimumFractionDigits: 2 })
                      : String(value)
                    }
                  </span>
                </div>
              ))}
            </div>

            <div className="flex gap-2 pt-4">
              <Button onClick={applyToCalculator} className="flex-1">
                Aplicar à Calculadora
              </Button>
              <Button variant="outline" onClick={clearFile}>
                Processar Nova Fatura
              </Button>
            </div>
          </CardContent>
        </Card>
      )}

      <Card>
        <CardHeader>
          <CardTitle className="text-sm">Dicas para Melhor Extração</CardTitle>
        </CardHeader>
        <CardContent>
          <ul className="text-sm text-gray-600 space-y-1">
            <li>• Certifique-se de que o PDF está legível e não é uma imagem escaneada</li>
            <li>• A fatura deve conter informações de consumo e demanda</li>
            <li>• Arquivos com melhor qualidade geram resultados mais precisos</li>
            <li>• O processamento pode levar alguns segundos dependendo do tamanho do arquivo</li>
          </ul>
        </CardContent>
      </Card>
    </div>
  );
}
